import { useHistory } from 'react-router-dom';
import { setCookie, isMobile } from '../../utils/utils'
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock'
import { TourProvider, useTour } from '@reactour/tour'
import './poolGuide.less'
import closeModal from '../../assets/img/close-modal-icon.svg'
const steps = [
  {
    selector: '.mining-claim',
    content: () => (
      <div className='step-one'>
        The old DERI farm is moved here
      </div>
    ),
  },
  {
    selector: '.showGuide',
    content: () => (
      <div className='step-one'>
        Click on the pool you’ve staked
      </div>
    ),
  },
  {
    selector: '.mining-deri-liquidity',
    content: () => (
      <div className='step-one'>
        The new DERI reward is shown here
      </div>
    ),
  },
]



export default function PoolGuide({ poolClaim, isThree = false }) {
  const { setIsOpen } = useTour()
  const history = useHistory();
  const Badge = ({ children }) => {
    return (
      <div className='step-num'><span className='now-step'>{children}</span>  / 3</div>
    )
  }
  const Close = ({ onClick }) => {
    const close = () => {
      onClick()
      setCookie('poolClaim', "true")
      sessionStorage.setItem('poolGuideThree', '0')
    }
    return (
      <img onClick={close} className='close-modal' src={closeModal}></img>
    )
  }



  const nextButton = ({ currentStep, stepsLength, setIsOpen, setCurrentStep, }) => {
    const last = currentStep === stepsLength - 1
    return (
      <button className='next' onClick={() => {
        if (last) {
          setIsOpen(false)
          setCookie('poolClaim', "true")
          sessionStorage.setItem('poolGuideThree', '0')
        } else {
          if (currentStep === 1) {
            let url = '/pool/v3/56/option/0x243681B8Cd79E3823fF574e07B2378B8Ab292c1E'
            history.push(url)
            sessionStorage.setItem('poolGuideThree', '3')
          }
          setCurrentStep(s => (s === steps.length - 1 ? 0 : s + 1))
        }
      }}>
        {last ? "OK" : "NEXT"}
      </button>
    )
  }

  const prevButton = ({ currentStep, stepsLength, setIsOpen, setCurrentStep, }) => {
    return (
      <>

      </>

    )
  }


  const disPlayBody = target =>  disableBodyScroll(target) 
  const enableBody = target => enableBodyScroll(target)
  return (
    <div>
      {poolClaim && <TourProvider
        steps={steps}
        startAt={isThree ? 2 : 0}
        defaultOpen={poolClaim}
        components={{ Close, Badge }}
        className='pool-guide'
        disableFocusLock={false}
        disableInteraction={true}
        onClickMask={() => { setIsOpen(true) }}
        afterOpen={disPlayBody}
        beforeClose={enableBody}
        scrollSmooth
        nextButton={nextButton}
        prevButton={prevButton}
      />}


    </div>
  )
}