import React, { useCallback, useEffect, useState, useMemo } from 'react'
import Button from "../Button/Button";
import { inject, observer } from 'mobx-react';
import version from '../../model/Version'
import ApiProxy from "../../model/ApiProxy";
import { openConfigListCache } from "../../lib/web3js/index";
import { isLP, firstLetterUppercase } from '../../utils/utils';
import Spinner from './Spinner'
import Icon from '../Icon/Icon'
import './transactionButton.less'


function TransactionButton({ lang, subject, wallet, pool = {}, ignoreApprove, bTokenId, bToken, icon, label, onClick, isReady, disabled = true, statusCallback, alert, ...restProps }) {
  const [enabled, setEnabled] = useState(true)
  const [btnText, setBtnText] = useState(label)
  const [initIsApprove, setInitIsApprove] = useState(false)
  const [pendingIsApprove, setPendingIsApprove] = useState(false)
  const [pendingIsTransaction, setPendingIsTransaction] = useState(false)
  const [pending, setPending] = useState(false)
  const [image, setImage] = useState(icon)

  const isApprove = useCallback(async (wallet, pool) => {
    let result
    if (wallet.detail && pool.address) {
      if (version.isOpen) {
        await openConfigListCache.update()
      }
      if (isLP(pool.address)) {
        result = await ApiProxy.request('isLpUnlocked', [wallet.detail.chainId, pool.address, wallet.detail.account])
      } else {
        if (bTokenId || bToken) {
          const params = [wallet.detail.chainId, pool.address, wallet.detail.account, bTokenId || bToken]
          result = await ApiProxy.request('isUnlocked', params)
        }
      }
    }
    return result;
  }, [bToken, bTokenId])

  const approve = useCallback(async () => {
    if (wallet && pool) {
      let approved;
      if (version.isOpen) {
        await openConfigListCache.update()
      }
      if (isLP(pool.address)) {
        approved = await ApiProxy.request('unlockLp', [wallet.detail.chainId, pool.address, wallet.detail.account], { write: true, subject: lang['approve'] });
      } else {
        let params = [wallet.detail.chainId, pool.address, wallet.detail.account, bTokenId || bToken]
        approved = await ApiProxy.request('unlock', params, { write: true, subject: firstLetterUppercase(lang['approve'].toLowerCase()) });
      }
      if (approved) {
        if (approved.status) {
          alert.success(`Approve ${bToken}`, {
            timeout: 8000,
            isTransaction: true,
            transactionHash: approved.transactionHash,
            link: `${wallet.blockExploreUrl}/tx/${approved.transactionHash}`,
            title: 'Approve Executed'
          })
        } else {
          if (approved.transactionHash === "") {
            return false;
          }
          alert.error(`Transaction Failed ${approved.error}`, {
            timeout: 300000,
            isTransaction: true,
            transactionHash: approved.transactionHash,
            link: `${wallet.blockExploreUrl}/tx/${approved.transactionHash}`,
            title: 'Approve Failed'
          })
        }
        return approved.status
      }
    }
  }, [pool, wallet, bTokenId, bToken])


  const click = async (e) => {
    if (pool && wallet && wallet.detail) {
      setPending(true)
      if (!wallet.isConnected()) {
        setImage(null)
        return wallet.connect()
      } else if (ignoreApprove || await isApprove(wallet, pool)) {
        setImage(icon)
        return onClick(e);
      } else {
        setImage(icon)
        let res = await approve()
        if (res) {
          setPendingIsApprove(true)
          let res = await onClick();
          if (res) {
            setPendingIsTransaction(true)
          }
          statusCallback && statusCallback(false)
        } else {
          setPending(false)
          // alert && alert.error('Approve failed')
          statusCallback && statusCallback(true)
        }
        return res;
      }
    }
  }

  useEffect(async () => {
    if (pool && wallet.detail && wallet.detail.account && pool.bTokens && lang) {
      if (!wallet.isConnected()) {
        setBtnText(lang['connect-wallet'])
        setImage(null)
      } else if (await isApprove(wallet, pool)) {
        setInitIsApprove(false)
        setBtnText(label)
        setImage(icon)
        statusCallback && statusCallback(false)
      } else {
        // setBtnText(lang['approve'])
        setInitIsApprove(true)
        setImage(icon)
        statusCallback && statusCallback(true)
      }
    }
  }, [pool, wallet, lang, bToken, bTokenId])

  useEffect(() => {
    setEnabled(!disabled)
  }, [disabled])

  useEffect(() => {
    if (wallet && pool && pool.address) {
      wallet.setAddress(pool.address);
      if (pool.isV3 || pool.isV3Lite) {
        wallet.setBToken(bToken)
      } else {
        wallet.setBTokenId(bTokenId)
      }
    }
  }, [wallet, pool, bTokenId, bToken])
  return (
    <div>
      <Button {...restProps} disabled={!enabled} label={btnText} onClick={click} icon={image} />
      {!ignoreApprove && initIsApprove && enabled && <div className='init-approve'>
        <div className='step-approve'>
          {!pending && <div className='step-one'>
            1
          </div>}
          {pending && <div className='pending'>
            {pendingIsApprove ? <Icon token='finish' /> : <Spinner />}
          </div>}
          <div className={pendingIsApprove ? 'approve-text finish-text' : 'approve-text'}>
            {lang['approve']}
          </div>
        </div>
        <div className={pendingIsApprove ? 'step-hr finish' : 'step-hr'}></div>
        <div className='step-transaction'>
          {!pendingIsApprove && <div className='step-two'>
            2
          </div>}
          {pending && pendingIsApprove && <div className='pending'>
            {pendingIsTransaction ? <Icon token='finish' /> : <Spinner />}
          </div>}
          <div className={pendingIsTransaction ? 'transaction-text finish-text' : 'transaction-text'}>
            {btnText}
          </div>
        </div>
      </div>}
    </div>
  )
}


export default inject('wallet')(observer(TransactionButton))