import { useState, useEffect } from 'react'
import UnderlineText from '../../../components/UnderlineText/UnderlineText'
import DeriNumberFormat from "../../../utils/DeriNumberFormat"
import downIcon from '../../../assets/img/arrow-down.svg'
import config from '../../../config.json'
import apiProxy from "../../../model/ApiProxy"
import Button from '../../../components/Button/Button'
import { useAlert } from 'react-alert'
import { formatAddress } from "../../../utils/utils"
import { DeriEnv } from '../../../lib/web3js/index'
const chainConfig = config[DeriEnv.get()]['chainInfo']
export default function PoolInfo({ wallet, chainId, nowApy, poolInfo, address, nowToken, lang, version, type, poolConfig, symbols }) {
  const [isShowInfo, setIsShowInfo] = useState('show')
  const [xvsNum, setXvsNum] = useState()
  const alert = useAlert();
  const getVenusEarned = async () => {
    console.log('poolConfig', poolConfig)
    if (poolConfig) {
      let res = await poolConfig.getVenusEarned(wallet, address)
      res = +res
      setXvsNum(res)
    }
  }
  const Collect = async () => {
    let res = await apiProxy.request('claimXVSLp', [wallet.detail.chainId, address, wallet.detail.account], {
      includeResponse: true,
      write: true,
      subject: lang['claim-XVS']
    })
    if (!res || !res.success) {
      if (res.response.transactionHash === "") {
        return false;
      }
      alert.error(`${lang['transaction-failed']} : ${res.response.error}`, {
        timeout: 300000,
        isTransaction: true,
        transactionHash: res.response.transactionHash,
        link: `${wallet.blockExploreUrl}/tx/${res.response.transactionHash}`,
        title: lang['claim-reward-executed']
      })
    } else {
      alert.success(lang['claim-XVS-reward'], {
        timeout: 8000,
        isTransaction: true,
        transactionHash: res.response.data.transactionHash,
        link: `${wallet.blockExploreUrl}/tx/${res.response.data.transactionHash}`,
        title: lang['claim-reward-failed']
      })
    }
    getVenusEarned()
  }
  useEffect(() => {
    if (wallet.isConnected()) {
      if (version === 'v3' || version === 'v3_lite') {
        let interval = null;
        getVenusEarned()
        interval = window.setInterval(() => {
          getVenusEarned()
        }, 1000 * 60 * 3);
        return () => {
          interval && clearInterval(interval);
        };
      }
    }
  }, [wallet, wallet.detail, poolConfig])
  return (
    <div className='pool-info'>
      <div className='pool-info-title'>
        <div className='pool-info-title-text'>
          {lang['pool-info']}
        </div>
        <div className='down-up'>
          {isShowInfo === 'show' && <span onClick={() => setIsShowInfo('hide')}>
            <img className='up-icon' alt='' src={downIcon} />
          </span>}
          {isShowInfo === 'hide' && <span onClick={() => setIsShowInfo('show')}>
            <img alt='' src={downIcon} />
          </span>}
        </div>
      </div>
      <div className={`'pool-info-text' ${isShowInfo}`}>
        <div className='pool-info-text-one'>
          {version !== 'v2_lite_open' && <div className='info-box'>
            <div className='info-box-title'>{lang['apy']}</div>
            <div className='info-box-num'>
              {(version !== 'v3' && version !== 'v3_lite') && <DeriNumberFormat value={poolInfo.apy} displayType='text' decimalScale={2} suffix='%' />}
              {(version === 'v3' || version === 'v3_lite') && <>
                <UnderlineText text={<DeriNumberFormat width='50px' value={nowApy.apy} displayType='text' decimalScale={2} suffix='%' />} tip={lang['deri-apy']} />
                &nbsp; +  &nbsp;  <UnderlineText text={<DeriNumberFormat width='50px' value={nowApy.supplyApy} displayType='text' decimalScale={2} suffix='%' />} tip={+chainId !== 42161 ? lang['venus-suppy-apy'] : lang['aave-suppy-apy']} />  &nbsp; {+chainId !== 42161 && '+'}  &nbsp;
                {+chainId !== 42161 && <UnderlineText text={<DeriNumberFormat width='50px' value={nowApy.xvsApy} displayType='text' decimalScale={2} suffix='%' />} tip={lang['xvs-distribution-apy']} />}
              </>}
            </div>
          </div>}
          <div className='info-box'>
            <div className='info-box-title'>{lang['pool-liquidity']}</div>
            <div className='info-box-num'>
              <DeriNumberFormat value={poolInfo.total} displayType='text' decimalScale={2} thousandSeparator={true} /> {(version === 'v3' || version === 'v3_lite') ? (+chainId === 56 || +chainId === 97) ? 'BUSD' : "USDC" : nowToken}
            </div>
          </div>
          {(version === 'v2' || version === 'v3' || version === 'v3_lite') && !(type === 'option' && version === 'v2') && <div className='info-box'>
            <div className='info-box-title'>{lang['multiplier']}</div>
            <div className='info-box-num'>{poolInfo.multiplier}</div>
          </div>}
          <div className='info-box'>
            <div className='info-box-title'>{lang['my-liquidity-percentage']}</div>
            <div className='info-box-num'><DeriNumberFormat allowZero={true} value={poolInfo.percent} decimalScale={2} suffix={'%'} /></div>
          </div>
          {((version !== 'v2' && version !== 'v3' && version !== 'v3_lite') || (version === 'v2' && type === 'option')) && <>
            <div className='info-box'>
              <div className='info-box-title'>{lang['liquidity-share-value']}</div>
              <div className='info-box-num'><DeriNumberFormat allowZero={true} decimalScale={6} value={poolInfo.shareValue} thousandSeparator={true} /> {nowToken}</div>
            </div>
          </>}
          <div className='info-box'>
            <div className='info-box-title'>{lang['staked-balance']}</div>
            <div className='info-box-num'>
              <DeriNumberFormat allowZero={true} value={poolInfo.formatShares} decimalScale={2} />
              {(version === 'v2' || version === 'v3' || version === 'v3_lite') && !(type === 'option' && version === 'v2') ? " " + nowToken : " " + lang['shares']}
            </div>
          </div>
          {((version !== 'v2' && version !== 'v3' && version !== 'v3_lite') || (version === 'v2' && type === 'option')) &&
            <div className='info-box'>
              <div className='info-box-title'></div>
              <div className='info-box-num'><DeriNumberFormat allowZero={true} decimalScale={2} value={poolInfo.values} thousandSeparator={true} /> {nowToken}</div>
            </div>
          }
          {(version === 'v2' || version === 'v3' || version === 'v3_lite') && !(type === 'option' && version === 'v2') && <div className='info-box'>
            <div className='info-box-title'>
              <UnderlineText multiline={true} text={lang['mining-pnl']} tip={lang['mining-pnl-hover']} html={true} element={<span> <div>{lang['mining-pnl-hover-one']} </div>{lang['mining-pnl-hover']} <div><a target="_blank" href='https://docs.deri.io/how-it-works/mining-amm-liquidity-mining#ii.-base-token-profit-with-market-risk'>{lang['learn-more-pnl']}</a> </div> </span>} />
            </div>
            <div className='info-box-num'>
              <DeriNumberFormat allowZero={true} prefix=' ' value={poolInfo.pnl} decimalScale={2} />  {(version === 'v3' || version === 'v3_lite') ? (+chainId === 56 || +chainId === 97) ? 'BUSD' : "USDC" : poolConfig && poolConfig.bTokens[0].bTokenSymbol} (<DeriNumberFormat value={poolInfo.pnlPercentage} decimalScale={2} suffix='%' />)
            </div>
          </div>}
          {(version === 'v3' || version === 'v3_lite') && (+chainId === 56 || +chainId === 97) && <div className='info-box'>
            <div className='info-box-title'>{lang['xvs-earned']}</div>
            <div className='info-box-num collect-box'>
              <DeriNumberFormat value={xvsNum} decimalScale={2} />
              <Button onClick={Collect} className='collect' label={lang['collect']} />
            </div>
          </div>
          }
        </div>
        <div className='pool-info-text-two'>
          {type !== 'lp' && <div className='info-box'>
            <div className='info-box-title'>{lang['symbols']}</div>
            <div className='info-box-num symbols'>
              <UnderlineText tip={symbols}>
                {symbols}
              </UnderlineText>
            </div>
          </div>}
          <div className='info-box'>
            <div className='info-box-title'>{lang['contract-address']}</div>
            <div className='info-box-num'>
              <a target='_blank' rel='noreferrer' href={`${chainConfig[chainId] && chainConfig[chainId]['viewUrl']}/address/${address}`}>
                {formatAddress(address)}
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}