import { useEffect, useState } from 'react'
import apiProxy from '../../model/ApiProxy'
import styled from 'styled-components'
import Icon from '../Icon/Icon';
import UnderlineText from '../UnderlineText/UnderlineText'
import DeriNumberFormat from '../../utils/DeriNumberFormat';

const Wrapper = styled.div`
  .remove-hint{
    cursor: pointer;
    .text{
      display: flex;
      border:0;
    }
    span{
      margin-left:8px;
    }
  }
`

export default function RemoveHint({ lang, wallet, baseToken, penalty, isB0Staked, availableBalance, getLang }) {
  const [text, setText] = useState('')
  const [b0Token,setb0Token] = useState('BUSD')
  useEffect(() => {
    let b0Token = (+wallet.detail.chainId === 56 || +wallet.detail.chainId === 97) ? 'BUSD' : 'USDC'
    setb0Token(b0Token)
    let label;
    let text = { num: baseToken,name:b0Token }
    let name = { name: b0Token}
    if (isB0Staked) {
      if (baseToken === 'BUSD' || baseToken === 'USDC') {
        label = `${lang['your-account-has-an-unsettled-balance-of']} ${penalty} ${b0Token} ${getLang('this-action-will-update-your',name)}`
      } else {
        label = `${lang['your-account-has-an-unsettled-balance-of']} ${penalty} ${b0Token} ${getLang('this-action-will-trigger-swapping-to-update-your', text)}`
      }
    } else {
      label = `${lang['your-account-has-an-unsettled-balance-of']} ${penalty} ${b0Token} ${getLang('this-action-will-trigger-swapping-to-update-your-busd-balance-to-reflect-the-unsettled-amount', text)}`
    }
    setText(label)
  }, [isB0Staked, penalty, baseToken])
  return (
    <Wrapper>
      {(+penalty) < 0 && (+availableBalance) > 0 && <div className='remove-hint'>
        <UnderlineText tip={text} width='332'>
          <Icon token='warn' /> <span>{lang['negative-unsettled-balance']} : <DeriNumberFormat value={penalty} decimalScale={2} /> {b0Token}</span>
        </UnderlineText>

      </div>}
    </Wrapper>

  )
}